.divider {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.black-button {
	margin-left: 15px;

	border-radius: 46px;

	width: 95px;
	height: 30px;

	background-color: #000000;

	color: #ffffff;
	font-size: 14px;
	font-weight: 500;
	
	display: flex;
	align-items: center;
	justify-content: center;
}

.black-button:hover {
	cursor: pointer;
	background-color: rgba(0, 0, 0, 0.7);
}

.disable-button {
	margin-left: 15px;

	border-radius: 46px;

	width: 95px;
	height: 30px;

	background-color: #000000;
	opacity: 0.4;

	color: #ffffff;
	font-size: 14px;
	font-weight: 500;
	
	display: flex;
	align-items: center;
	justify-content: center;
}

.disable-button:hover {
	cursor: not-allowed;
}

.white-button {
	margin-left: 10px;

	border: 1px solid rgba(0, 0, 0, 0.5);
	border-radius: 46px;

	width: 93px;
	height: 28px;

	background-color: #ffffff;

	color: rgba(0, 0, 0, 0.5);
	font-size: 14px;
	font-weight: 500;
	
	display: flex;
	align-items: center;
	justify-content: center;
}

.white-button:hover {
	cursor: pointer;
	background-color: rgba(240, 240, 240, 0.7);
}