#auth-wrapper {
	padding: 100px 8.333333vw 150px;
	
	min-height: 960px;
	height: calc(100vh - 64px - 100px - 150px);

	background-image: url(asset/auth_background.png);
	background-position: 50% 50%;
	background-size: 100% 100%;

	display: flex;
}

.auth-left {
	flex: 7;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.auth-left-icon {
	width: 50px;
	height: 50px;

	background-image: url('asset/auth_logo.png');
	background-position: 50% 50%;
	background-size: 100% 100%;
}

.auth-left-text-container {
	margin-top: 8px;
}

.auth-left-text {
	margin-top: 12px;

	color: #ffffff;
	font-family: 'GimhaeGaya';
	font-size: 40px;
	font-weight: 400;
	letter-spacing: -1.2px;
	line-height: 100%;

	text-align: center;
}

.auth-right {
	flex: 5;
}

.auth-card {
	border-radius: 10px;

	padding: 50px 50px 30px;

	height: calc(100% - 50px - 30px);

	background-color: #ffffff;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.auth-card-top-text-container {
	display: flex;
	justify-content: space-between;
}

.auth-card-top-text {
	font-size: 24px;
	font-weight: 500;
}

.auth-card-top-text-opacity {
	font-size: 24px;
	font-weight: 500;
	opacity: 0.7;
}

.auth-card-bottom {
	display: flex;
	flex-direction: column;
}

.auth-card-bottom-error-message {
	margin-bottom: 16px;

	color: rgba(255, 0, 0, 0.7);
	
	font-size: 12px;
	font-weight: 400;

	white-space: pre-line;		/* 문자열로 되어있는 줄바꿈, 띄어쓰기(\n)에 대해 줄바꿈 처리를 해줌 */
}

.auth-card-bottom-button {
	border-radius: 46px;
	padding: 14px 0;

	width: 100%;
	height: 20px;

	display: flex;
	justify-content: center;
	align-items: center;

	background-color: #000000;
	color: #ffffff;

	font-size: 14px;
	font-weight: 500;
}

.auth-card-bottom-button:hover {
	cursor: pointer;
	background-color: rgba(0, 0, 0, 0.7);
}

.auth-card-bottom-text {
	margin-top: 20px;

	color: rgba(0, 0, 0, 0.7);
	font-size: 14px;
	font-weight: 400;

	text-align: center;
}

.auth-emphasis {
	color: #000000;
	font-weight: 500;
}

.auth-emphasis:hover {
	cursor: pointer;
}